import React, { Component } from "react";
import { connect } from "react-redux";
import {
  filterProducts,
  sortProducts,
  filterCategory,
  filterSearch,
} from "../actions/auth";
class Filter extends Component {
  state = {
    search: "",
  };
  handleChange = (e) => {
    this.setState({ search: e.target.value });
  };
  search = () => {
    var subcat = this.state.search;
    subcat = subcat.toLowerCase();
    if (subcat) {
      //tshirt
      if (
        subcat == "tunic" ||
        subcat == "tunics" ||
        subcat == "tshirt" ||
        subcat == "tshirts"
      ) {
        subcat = "tshirt";
      }
      //jeans
      else if (
        subcat == "jean" ||
        subcat == "jeans" ||
        subcat == "pants" ||
        subcat == "pant"
      ) {
        subcat = "jeans";
      }
      //jackets
      else if (
        subcat == "jacket" ||
        subcat == "jackets" ||
        subcat == "coats" ||
        subcat == "coat"
      ) {
        subcat = "jackets";
      }
      //shrugs
      else if (subcat == "shrug" || subcat == "shrugs") {
        subcat = "shrugs";
      }
      //kurtis
      else if (
        subcat == "kurti" ||
        subcat == "top" ||
        subcat == "kurtis" ||
        subcat == "tops"
      ) {
        subcat = "kurtis";
      }
      //sarees
      else if (subcat == "saree" || subcat == "sarees") {
        subcat = "sarees";
      }
      //dresses
      else if (subcat == "dress" || subcat == "dresses") {
        subcat = "dresses";
      }
      //lehengas
      else if (
        subcat == "lehenga" ||
        subcat == "lehengas" ||
        subcat == "party wear"
      ) {
        subcat = "lehengas";
      }
      //flats
      else if (
        subcat == "flat" ||
        subcat == "flats" ||
        subcat == "chappals" ||
        subcat == "chappal"
      ) {
        subcat = "flats";
      }
      //heels
      else if (subcat == "heel" || subcat == "heels") {
        subcat = "heels";
      }
      //shoes
      else if (
        subcat == "shoe" ||
        subcat == "shoes" ||
        subcat == "casuals" ||
        subcat == "casual" ||
        subcat == "sports" ||
        subcat == "sport"
      ) {
        subcat = "shoes";
      }
      //sandals
      else if (subcat == "sandal" || subcat == "sandals") {
        subcat = "sandals";
      } else {
        subcat = "";
        alert("OOPS! Didn't found you match");
      }
    } else {
      subcat = "";
    }

    this.props.filterSearch(this.props.products, subcat);
  };

  render() {
    return (
      <div>
        <div className="row">
          <div
            className="col-md-3"
            style={{
              fontWeight: "bold",
              fontFamily: "cursive",
              fontSize: "18pt",
            }}
          >{`${this.props.filteredProducts.length} products found.`}</div>
          <div className="col-md-3">
            <label>
              {" "}
              Category
              <select
                className="form-control"
                value={this.props.cat}
                onChange={(event) => {
                  this.props.filterCategory(
                    this.props.products,
                    event.target.value
                  );
                }}
                /* onChange={(event) => event.target.value} */
              >
                <option value="">ALL</option>
                <option value="Ethnic">Ethnic</option>
                <option value="Western">Western</option>
                <option value="Footwear">Footwear</option>
              </select>
            </label>
          </div>
          <div className="col-md-3">
            <label>
              Price
              <select
                className="form-control"
                value={this.props.sort}
                onChange={(event) => {
                  this.props.sortProducts(
                    this.props.filteredProducts,
                    event.target.value
                  );
                }}
              >
                <option value="">ALL</option>
                <option value="lowestprice">Lowest to highest</option>
                <option value="highestprice">Highest to lowest</option>
              </select>
            </label>
          </div>
          {/* <div className="col-md-3">
            <label>
              {" "}
              Filter Size
              <select
                className="form-control"
                value={this.props.size}
                onChange={(event) => {
                  this.props.filterProducts(
                    this.props.products,
                    event.target.value
                  );
                }}
              >
                <option value="">ALL</option>
                <option value="x">XS</option>
                <option value="s">S</option>
                <option value="m">M</option>
                <option value="l">L</option>
                <option value="xl">XL</option>
                <option value="xxl">XXL</option>
              </select>
            </label>
          </div> */}
        </div>
        {/* <div className="col-md-4">
          <input
            type="text"
            class="form-control "
            placeholder="Search"
            value={this.state.search}
            onChange={this.handleChange}
          />
          <button class="btn btn-success " onClick={this.search}>
            Search
          </button>
        </div> */}
        <div class="input-group mb-3">
          <input
            type="text"
            class="form-control"
            placeholder="Search"
            value={this.state.search}
            onChange={this.handleChange}
          />
          <div class="input-group-append">
            <button class="btn btn-success" onClick={this.search}>
              Search
            </button>
          </div>
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  products: state.cartReducer.items,
  filteredProducts: state.cartReducer.filteredItems,
  size: state.cartReducer.size,
  sort: state.cartReducer.sort,
  cat: state.cartReducer.cat,
});
export default connect(mapStateToProps, {
  /* filterProducts, */
  sortProducts,
  filterCategory,
  filterSearch,
})(Filter);
